<script>
  import SectionHeading from "../shared/SectionHeading.svelte";
  import axios from "axios";
  import theme from "../stores/theme";
  $: bg = $theme.mode == "dark" ? "bg-brand-darker-black" : "bg-gray-200";

  let sending = false;

  let message = {
    name: "",
    email: "",
    tel: "",
    object: "",
    content: "",
  };

  let handleSubmit = () => {
    sending = true;

    const headers = {
      "Content-Type": "application/json",
      Authorization: `Bearer ${process.env.SEND_GRID_KEY}`,
    };

    var data = {
      personalizations: [
        {
          to: [
            {
              email: "katendeglory@gmail.com",
            },
          ],
        },
      ],
      from: {
        email: "glorykat777@gmail.com",
      },
      subject: `🔔 PORTFOLIO ALERT 🔔 From ${message.name} on The Subject ${message.object}`,
      content: [
        {
          type: "text/plain",
          value: `
Name : ${message.name}\n
Telephone : ${message.tel}\n
Email : ${message.email}\n
Subject : ${message.object}\n
------------------\n\n\n
${message.content}
          `,
        },
      ],
    };

    axios
      .post(
        "https://cors-anywhere.herokuapp.com/https://api.sendgrid.com/v3/mail/send",
        data,
        { headers }
      )
      .then((response) => {
        sending = false;
        alert(
          `Thank You For the Message ${message.name}, I'll get back to you`
        );
        message.content = "";
        message.email = "";
        message.name = "";
        message.object = "";
        message.tel = "";
      })
      .catch((error) => {
        sending = false;
        alert(
          `Sorry, There was an unexpected Error 😞, In a sharing mood? Please report it to katendeglory@gmail.com, I'll fix it so others won't have the same issue`
        );
        console.log(error.message);
      });
  };
</script>

<style>
  .contact-card {
    /* border-top-right-radius: 50px; */
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
  }

  @media (min-width: 768px) {
    .contact-card {
      border-radius: 0;
      border-top-right-radius: 25px;
      border-bottom-left-radius: 25px;
    }
  }
</style>

<div class="relative" id="contacts">
  <div class="max-w-7xl mx-auto px-4 py-6 overflow-x-hidden" id="my-work">

    <SectionHeading text1="REACH" text2="ME" />

    <h1
      class="text-base mt-16 mb-24 max-w-2xl lg:text-center mx-auto"
      data-aos="fade-up">
      Hey there! Thanks for visiting my site!
      <div class="lg:hidden mb-3" />
      Just fill the form below 👇 or give me a call 🤙.
      <div class="mb-3" />
      And I promise I'll get back to you as soon as I can, and hear all about
      your amazing ideas 😉!
    </h1>

    <div class="flex flex-col md:flex-row">
      <div
        data-aos="fade-right"
        class={`contact-card overflow-hidden ${bg} flex flex-col shadow-md relative max-w-sm mx-auto`}>
        <!-- Top -->
        <div class="w-full h-56 overflow-hidden relative">
          <img
            src="./images/profile.jpg"
            class="w-full h-full object-cover object-top shadow-lg"
            alt="Glory Katende" />
          <div
            class="w-full h-full absolute bg-brand-green top-0 left-0"
            style="opacity:.10;" />
        </div>
        <!-- Bottom -->
        <div
          class="w-full pl-10 pt-6 pb-10 border-t-4 border-brand-red flex
          flex-col justify-center">
          <div class="flex items-center mb-4">
            <i class="fas fa-phone-square text-brand-green text-5xl mr-3" />
            <a href="tel:+26878635116">268 78635116</a>
          </div>
          <div class="flex items-center mb-4">
            <i class="fab fa-whatsapp text-brand-green text-5xl mr-3" />
            <a href="https://wa.me/26878635116" target="_blank">268 78635116</a>
          </div>
          <div class="flex items-center mb-4">
            <i class="fas fa-envelope-square text-brand-green text-5xl mr-3" />
            <a href="mailto:katendeglory@gmail.com" target="_blank">
              katendeglory@gmail.com
            </a>
          </div>
          <!-- <div class="flex items-center mb-4">
            <i class="fab fa-linkedin text-brand-green text-5xl mr-3" />
            <a href="https://www.linkedin.com/in/glorykatende/" target="_blank">
              Glory Katende
            </a>
          </div> -->
        </div>

        <img
          src="./images/card-tablets-yellow.svg"
          class="absolute left-0 opacity-50"
          style="top:50%; transform: translate(-10px, -2%);"
          alt="tablets" />
      </div>

      <form
        data-aos="fade-left"
        on:submit|preventDefault={handleSubmit}
        class="w-full mt-8 md:mt-0 md:ml-6">
        <div class="lg:flex">
          <input
            bind:value={message.name}
            type="text"
            class={`text-field w-full lg:w-1/2 lg:mr-4 mb-4 ${bg}`}
            required
            placeholder="Your Name" />
          <input
            bind:value={message.tel}
            type="tel"
            class={`text-field w-full lg:w-1/2 mb-4 ${bg}`}
            required
            placeholder="Your Cell (With Country Code)" />
        </div>

        <div class="lg:flex">
          <input
            bind:value={message.email}
            type="email"
            class={`text-field w-full lg:w-1/2 lg:mr-4 mb-4 ${bg}`}
            required
            placeholder="Your Email Address" />
          <input
            bind:value={message.object}
            type="text"
            class={`text-field w-full lg:w-1/2 mb-4 ${bg}`}
            required
            placeholder="The Object of your message" />
        </div>

        <div class="">
          <textarea
            bind:value={message.content}
            class={`text-field w-full mb-4 ${bg}`}
            required
            rows="6"
            placeholder="Enter your message here..." />
        </div>

        <div class="w-full flex justify-end">
          <button
            disabled={sending}
            class={`text-field text-brand-green flex items-center text-lg ${bg} w-full`}>
            {#if sending}
              Sending...
              <i class="ml-3 fas fa-circle-notch animate-spin fa-lg" />
            {:else}
              Send Your Message
              <i class="ml-3 fas fa-envelope fa-lg" />
            {/if}
          </button>
        </div>

        <h1 class="mt-8 opacity-50">* I hereby declare to never spam you!</h1>
      </form>

    </div>
  </div>

  <!-- Absolute elements -->
</div>
